html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-family-basic);
}

h1,
h2,
h3 {
  margin: 0;
}

figure {
  margin: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --light-primary-1: hsl(0, 0%, 100%);
  --light-primary-2: hsl(204, 15%, 94%);
  --light-primary-3: hsl(198, 16%, 84%);
  --light-primary-4: hsl(200, 9%, 52%);
  --light-color-text: hsl(0, 0%, 13%);
  --dark-primary-1: hsl(0, 0%, 13%);
  --dark-primary-2: hsl(200, 19%, 18%);
  --dark-primary-3: hsl(200, 18%, 26%);
  --dark-primary-4: hsl(199, 18%, 33%);
  --dark-color-text: hsl(0, 0%, 100%);
  --title-color: #75eab0;
  --title-color-1: #007c4a;
  --error-1: hsl(0, 76%, 41%);
  --error-2: hsl(0, 100%, 50%);
  --success-1: hsl(110, 45%, 43%);
  --success-2: hsl(121, 67%, 22%);
  --warning-1: hsl(20, 100%, 63%);
  --warning-2: hsl(20, 76%, 53%);
  --input-border-1: hsl(199, 18%, 33%);
  --input-border-2: hsl(0, 0%, 13%);
  --input-color-1: hsl(200, 19%, 18%);
  --input-color-2: hsl(0, 0%, 13%);
}

@media (prefers-color-scheme: dark) {
  :root {
    --body-background: var(--dark-primary-1);
    --background-1: var(--dark-primary-1);
    --background-2: var(--dark-primary-2);
    --text-color: var(--dark-color-text);
    --text-color2: var(--light-primary-4);
    --card-bg: var(--dark-primary-3);
    --card-title: var(--title-color);
    --toggler-border: var(--light-primary-1);
    --toggler-background: var(--dark-primary-2);
    --toggler-button: var(--light-primary-4);
    --hover-background: var(--light-primary-4);
    --hover-text: var(--dark-primary-2);
    --error-color: var(--error-1);
    --success-color: var(--success-1);
    --warning-color: var(--warning-1);
    --input-border: var(--input-border-1);
    --input-color: var(--input-color-1);
    --navProf-background: var(--light-primary-1);
    --navProf-background-hover: var(--dark-primary-2);
    --navProf-text: var(--light-color-text);
    --navProf-text-hover: var(--light-primary-2);
    --input-text-color: var(--text-color);
    --postcard-background-color: var(--dark-primary-2);
    --postcard-color: var(--light-primary-3);
    --postcard-link-background: var(--dark-primary-4);
    --postcard-link-text: var(--light-primary-1);
    --postcard-button-background: var(--light-color-text);
  }
}
body.dark {
  --body-background: var(--dark-primary-1);
  --background-1: var(--dark-primary-1);
  --background-2: var(--dark-primary-2);
  --text-color: var(--dark-color-text);
  --text-color2: var(--light-primary-4);
  --card-bg: var(--dark-primary-3);
  --card-title: var(--title-color);
  --toggler-border: var(--light-primary-1);
  --toggler-background: var(--dark-primary-2);
  --toggler-button: var(--light-primary-4);
  --hover-background: var(--light-primary-4);
  --hover-text: var(--dark-primary-2);
  --error-color: var(--error-2);
  --success-color: var(--success-1);
  --warning-color: var(--warning-1);
  --input-border: var(--input-border-1);
  --input-color: var(--input-color-1);
  --navProf-background: var(--light-primary-1);
  --navProf-text: var(--light-color-text);
  --input-text-color: var(--text-color);
  --postcard-background-color: var(--dark-primary-2);
  --postcard-color: var(--light-primary-3);
  --postcard-link-background: var(--dark-primary-4);
  --postcard-link-text: var(--light-primary-1);
}

body.light {
  --body-background: var(--light-primary-1);
  --background-1: var(--light-primary-2);
  --background-2: var(--light-primary-4);
  --text-color: var(--light-color-text);
  --text-color-2: var(--dark-primary-2);
  --card-bg: var(--light-primary-4);
  --card-title: var(--title-color-1);
  --toggler-border: var(--dark-primary-1);
  --toggler-background: var(--light-primary-4);
  --toggler-button: var(--dark-primary-2);
  --hover-background: var(--dark-primary-2);
  --hover-text: var(--light-primary-3);
  --error-color: var(--error-2);
  --success-color: var(--success-2);
  --warning-color: var(--warning-2);
  --input-border: var(--input-border-2);
  --input-color: var(--input-color-2);
  --navProf-background: var(--dark-primary-1);
  --navProf-text: var(--dark-color-text);
  --input-text-color: var(--light-primary-1);
  --postcard-background-color: var(--light-color-text);
  --postcard-color: var(--light-primary-1);
  --postcard-link-background: var(--dark-primary-3);
  --postcard-link-text: var(--light-primary-2);
}

body {
  background-color: var(--body-background);
  transition: background-color 0.3s ease;
}

:root {
  --font-family-basic: -apple-system,
  BlinkMacSystemFont,
  Segoe UI,
  Roboto,
  Oxygen,
  Ubuntu,
  Cantarell,
  Fira Sans,
  Droid Sans,
  Helvetica Neue,
  sans-serif;
}

body {
  font-family: var(--font-family-basic);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading {
  width: 2rem;
  height: 2rem;
  padding: 1rem !important;
  border-radius: 50% !important;
  border: 10px solid var(--background-2) !important;
  border-top: 10px solid var(--text-color) !important;
  background-color: var(--toggler-button) !important;
  pointer-events: none;
  animation: spin 2s linear infinite;
  background-color: inherit;
}

.toggler {
  border: none;
  display: flex;
  align-items: center;
}
.toggler .toggler__wrapper {
  position: relative;
}
.toggler .toggler__wrapper input[type=radio] {
  display: none;
}
.toggler .toggler__background {
  border: 1px solid var(--toggler-border);
  background-color: var(--toggler-background);
  width: 5rem;
  height: 2rem;
  margin-left: 1rem;
  margin-right: 1rem;
  border-radius: 1rem;
  display: block;
  z-index: 1;
  position: relative;
}
.toggler .toggler__button {
  width: 1.5rem;
  height: 1.5rem;
  background-color: var(--toggler-button);
  border: none;
  border-radius: 50%;
  position: absolute;
  transition: all 0.3s ease;
  cursor: pointer;
  z-index: 2;
}
.toggler .toggler__button--light {
  top: 50%;
  left: 95%;
  transform: translate(-160%, -50%);
}
.toggler .toggler__button--dark {
  top: 50%;
  left: 95%;
  transform: translate(-360%, -50%);
}
.toggler .toggler__label {
  color: var(--text-color);
  font-size: 1.1rem;
  font-weight: 550;
  cursor: pointer;
}

.nft {
  width: 55%;
  display: block;
  background-color: var(--card-bg);
  padding: 2rem;
  border-radius: 1rem;
}
.nft .nft__title {
  font-size: 1.75rem;
  color: var(--card-title);
  margin-bottom: 2rem;
}
.nft .nft__text {
  color: var(--text-color);
  font-size: 1.1rem;
  word-spacing: 1px;
  letter-spacing: 1.1px;
  line-height: 1.5;
}

.formLink {
  margin: 0 auto;
  margin-top: 3rem;
  width: max-content;
  padding-top: 0.65em;
  padding-bottom: 0.65em;
  padding-left: 1.25em;
  padding-right: 1.25em;
  font-size: 1.1rem;
  background-color: var(--background-2);
  border: none;
  outline: none;
  cursor: pointer;
  color: var(--text-color);
  transition: all 0.3s ease;
}
.formLink:hover {
  background-color: var(--hover-background);
  color: var(--hover-text);
}

.formInput {
  height: 2.25rem;
  font-size: 1.15rem;
  width: 14em;
  text-align: center;
  outline: none;
  border: 2px solid var(--input-border);
  color: var(--input-color);
  -webkit-transition: background-color 0.3s ease, color 0.3s ease;
  -moz-transition: background-color 0.3s ease, color 0.3s ease;
  -ms-transition: background-color 0.3s ease, color 0.3s ease;
  -o-transition: background-color 0.3s ease, color 0.3s ease;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.formLabel {
  font-size: 1.1rem;
  color: var(--text-color);
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.form {
  display: flex;
  flex-direction: column;
  min-width: 50rem;
  max-width: 50rem;
  margin: 0 auto;
  align-items: center;
  margin-top: 3rem;
}

.formTitle {
  font-size: 2.5rem;
  color: var(--text-color);
}

.form ~ footer {
  position: absolute;
  bottom: 0;
}

.formError {
  font-size: 1.1rem;
  color: var(--error-color);
}

.form__note {
  color: var(--text-color);
  margin-top: 1rem;
  font-size: 1.075rem;
}

.formError {
  color: var(--error-color);
  margin-top: 2rem;
  font-size: 1.175rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.navUser {
  cursor: pointer;
  padding-left: 4rem;
  padding-right: 4rem;
  padding-top: 0.1rem;
  padding-bottom: 0.1rem;
  border-radius: 0.75rem;
  color: var(--navProf-text);
  background-color: var(--navProf-background);
  font-size: 1.1rem;
  transition: all 0.3s ease;
}
.navUser:hover {
  background-color: var(--navProf-background-hover);
  color: var(--navProf-text-hover);
}

.camera__error {
  max-width: max-content;
  text-align: center;
  padding: 0.5rem;
  background-color: #e26a2c;
  color: var(--text-color);
  border-radius: 0.5rem;
  margin-top: 1rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1rem;
}

.DaD {
  width: max-content;
  padding: 1rem;
  background-color: var(--background-2);
  margin-top: 2rem;
  margin-bottom: 2rem;
  color: var(--text-color);
  transition: all 0.3s ease;
}
.DaD__paragraph {
  margin-bottom: 2.25rem;
}
.DaD__input, .DaD__label {
  cursor: pointer;
}
.DaD__label {
  border: 1px solid var(--text-color);
  padding: 0.4rem;
  transition: all 0.3s ease;
}
.DaD__label:hover {
  background-color: var(--text-color);
  color: var(--background-2);
}
.DaD--highlight {
  background-color: var(--background-1);
}

.gallery {
  display: grid;
  grid-auto-rows: 20rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  row-gap: 1rem;
  column-gap: 1rem;
}
.gallery--single {
  display: block;
  width: 40rem;
  height: 20rem;
  margin: 0 auto;
}
.gallery--odd {
  grid-template-columns: repeat(3, 26.66rem);
}
.gallery--even {
  grid-template-columns: repeat(2, 40rem);
}
.gallery__figure {
  width: 100%;
  height: 100%;
  display: block;
}
.gallery__image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.button {
  padding: 0.625rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: normal;
  background-color: var(--background-2);
  color: var(--text-color);
  transition: all 0.3s ease;
  margin-top: 1rem;
}
.button:hover {
  background-color: var(--hover-background);
  color: var(--hover-text);
}

.notification {
  display: block;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 0.75rem;
  padding-right: 0.75rem;
  font-size: 1rem;
  transition: all 0.3s ease;
  width: 24rem;
  margin: 0 auto;
  margin-top: 2rem;
  margin-bottom: 2rem;
  word-break: break-word;
  text-align: center;
  position: relative;
}
.notification--negative {
  background-color: var(--error-color);
}
.notification--positive {
  background-color: var(--success-color);
}
.notification--warning {
  background-color: var(--warning-color);
}
.notification__button {
  width: 2rem;
  height: 2rem;
  padding: 0.25em;
  outline: none;
  border: none;
  border-radius: 50%;
  position: absolute;
  left: 100%;
  top: -5%;
  transform: translate(-50%, -5%);
  font-size: 1rem;
  cursor: pointer;
  background-color: var(--text-color);
  color: var(--background-1);
  transition: all 0.3s ease;
}
.notification__button:hover {
  background-color: var(--background-1);
  color: var(--text-color);
}
.notification__container {
  position: absolute;
  top: 25%;
  left: 1%;
}

.postcardDescription {
  padding: 1rem;
  font-size: 1rem;
  margin-top: 1rem;
  display: block;
  width: 22.5rem;
  text-align: center;
  border-radius: 1.5rem;
  outline: none;
  border: 1.5px solid transparent;
  transition: all 0.3s ease;
  background-color: var(--input-border);
  color: var(--input-text-color);
}

.postcards {
  display: block;
  width: 40%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  grid-template-rows: auto;
  margin-top: 5rem;
  text-align: center;
}
.postcards__item {
  background-color: var(--postcard-background-color);
  padding: 1rem;
  color: var(--text-color);
}
.postcards__id {
  font-size: 1.7rem;
  letter-spacing: 1.4px;
}
.postcards__description {
  text-align: center;
  padding: 0.5rem;
  background-color: var(--postcard-link-background);
}
.postcards__link {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.postcards__linkText {
  padding: 0.5rem;
  flex-grow: 2;
  background-color: var(--postcard-link-background);
  text-align: left;
  text-decoration: underline;
  text-decoration-color: var(--postcard-link-text);
  color: var(--postcard-link-text);
}
.postcards__button {
  z-index: 2;
  margin-left: auto;
  cursor: pointer;
  font-size: 1.3rem;
  background-color: var(--postcard-button-background);
  padding: 0.3rem;
}

.postcards .footer {
  position: absolute;
  bottom: 0;
}

.navigation {
  padding: 0.4em;
  background-color: var(--background-1);
  transition: background-color 0.3s ease;
}
.navigation__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  list-style-type: none;
}
.navigation__link {
  color: var(--text-color);
  font-size: 1.1rem;
  font-weight: 550;
  transition: color 0.3s ease;
}

.header {
  margin-top: 2rem;
  display: flex;
}
.header__text {
  margin-top: 10rem;
  padding-left: 2rem;
  color: var(--text-color);
}
.header__title {
  font-size: 5rem;
}
.header__info {
  font-size: 1.25rem;
  padding-left: 2rem;
  white-space: nowrap;
}
.header__images {
  margin-left: 2rem;
  border: 3px solid var(--text-color);
  position: relative;
  display: grid;
  grid-template-columns: 25rem 34rem;
  grid-template-rows: repeat(2, 25rem);
}
.header__figure {
  position: relative;
}
.header__figure--left {
  width: 25rem;
  height: 50rem;
  grid-column: 1/2;
  grid-row: 1/3;
}
.header__figure--bottomRight {
  width: 34rem;
  height: 25rem;
  grid-row: 1/2;
  grid-column: 2/3;
}
.header__figure--upRight {
  width: 34rem;
  height: 25rem;
  grid-row: 2/3;
  grid-column: 2/3;
}
.header__figure:hover {
  z-index: 4;
}

.main {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 2rem;
  justify-items: center;
  margin-top: 14rem;
}
.main__title {
  font-size: 3rem;
  color: var(--text-color);
  text-align: center;
  margin-bottom: 3rem;
  grid-column: 1/3;
}
.main__linksContainer {
  grid-column: 1/3;
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  margin-bottom: 3rem;
}
.main__link {
  text-decoration: none !important;
  font-size: 1.3rem;
  padding: 1rem;
  background-color: var(--background-2);
  margin-top: 2rem;
  text-align: center;
  color: var(--text-color);
  transition: all 0.3s ease;
}
.main__link:hover {
  background-color: var(--hover-background);
  color: var(--hover-text);
}

.footer {
  width: 100%;
  background-color: var(--background-1);
  color: var(--text-color);
  font-size: 1.2rem;
  text-align: center;
  padding: 2rem;
  margin-top: 2rem;
}

.login {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.user {
  margin-top: 3rem;
  width: 100%;
}
.user__navigation {
  position: relative;
}
.user__navigation::before {
  width: 30%;
  content: "";
  height: 1px;
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%, 0%);
  background-color: var(--text-color);
}
.user__navigation::after {
  width: 40%;
  content: "";
  height: 1px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -100%);
  background-color: var(--text-color);
}
.user__navList {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  list-style-type: none;
  height: 100%;
}
.user__link {
  display: inline-block;
  padding: 1rem;
  font-size: 1.075rem;
  height: 100%;
  color: var(--text-color);
  font-weight: 525;
  position: relative;
  z-index: 3;
  transition: all 0.3s ease;
}
.user__link--selected::after {
  height: 100% !important;
}
.user__link--selected {
  color: var(--background-1) !important;
}
.user__link::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 0%;
  transform: translate(0%, -100%);
  width: 100%;
  height: 0;
  z-index: -1;
  background-color: var(--text-color);
  transition: all 0.3s ease;
}
.user__link:hover {
  color: var(--background-1);
}
.user__link:hover::after {
  height: 100%;
}
.user__location {
  color: var(--text-color);
  width: max-content;
  display: block;
  padding: 0.75rem;
  border-radius: 0.5rem;
  border: 1px solid var(--text-color);
  margin: 0 auto;
  margin-top: 2rem;
}
.user__main {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}